.messages {
    max-height: 500px;
    overflow-y: auto;
    border: 2px solid darkgrey;
}

.message {
    width: 100%;
    height: 50px;
    margin-top: 10px;
    font-size: 20px;
    line-height: 30px;
    background-color: #ccc;

    &:first-of-type {
        margin-top: 0;
    }
}